import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Sign from '@/views/sign'
import Success from '@/views/success'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Sign',
      component: Sign
    },
    {
      path: '/success',
      name: 'Success',
      component: Success
    }
  ]
})



