
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

var localstroage = window.localStorage;
export default {
components: {
Toast,
XInput,
XButton,
Group,
Cell,
Checker,
CheckerItem,
Checklist
},
data() {
return {
show1: false,
show2: false,
show3: false,
show4: false,
show5: false,
error: "",
vaild1: false,
vaild2: false,
vaild3: false,
vaild4: false,
vaild5: false,
phone: '',
name: '',
college: '',
grades: '',
studentid: '',
password: '',
demo2: ['stud'],
commonList: [{
key: 'stud',
value: ''
}, {
key: 'parent',
value: ''
}],
}
},
methods: {
click() {



let data = new FormData();


data.append('name', this.name);
data.append('phone', this.phone);
data.append('studentid', this.studentid);
data.append('college', this.college);
data.append('grades', this.grades);
if(this.$route.query.from!=null){
data.append('from',this.$route.query.from);
}
this.valid1 = this.$refs.input01.valid;
this.valid2 = this.$refs.input02.valid;
this.valid3 = this.$refs.input03.valid;
this.valid4 = this.$refs.input04.valid;
this.valid5 = this.$refs.input05.valid;

if(!this.valid1||!this.valid2||!this.valid3||!this.valid4||!this.valid5){
this.show4=true;
}else if(this.name==""||this.phone==""||this.studentid==""||this.college==""||this.grades==""){
this.show3=true;
}else{
axios.post('api/sign', data).then(response => {
if(response.data=="ok"){
this.$router.push('/success?name='+this.name+'&phone='+this.phone+'&studentid='+this.studentid+'&college='+this.college+'&grades='+this.grades);
}else{
let k = response.data;
if(k.phone!=null){
this.error = "";
}else if(k.studentid!=null){
this.error = "";
}else{
this.error = k;
}
console.log(k.phone);
this.show5=true;

}

})
}

}
}
}



